/*--- Header ---*/
.header {
  width: 100%;
  height: 55px;
  padding: 8px 14px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  color: #eee;
  background-color: #333;
}
.header svg {
  width: 2rem;
  height: 2rem;
  margin-right: 14px;
}
.heading span {
  font-size: 1.55rem;
}
.head-logo-container {
  display: flex;
  align-items: center;
  justify-content: center;
}
/*--- Nav ---*/
.navbar {
  width: 400px;
  height: 100%;
  margin-right: 40px;
}
.nav {
  width: 100%;
  height: 100%;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.a {
  font-size: 18px;
  color: #eee;
  text-decoration: none;
}
.a:hover {
  color: #ff8700;
  opacity: 0.8;
}
.active-link {
  color: #ff8700;
}
/*--- Container ---*/
.container {
  width: 100vw;
  height: calc(100vh - 55px);
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #aaa;
  position: relative;
}
/*--- Home ---*/
.card {
  width: 800px;
  height: 425px;
  margin: 0 auto;
  padding: 8px 20px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 5px 8px 5px #888;
  position: relative;
}
.home .location-section p:nth-child(1) {
  font-size: 2.4rem;
}
.home .data-section {
  width: 100%;
  height: 65%;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.home .data-section .temp-data,
.home .data-section .weather-data {
  flex-grow: 1;
  height: 100%;
}
.home .data-section .temp-data {
  display: flex;
  align-items: center;
  justify-content: center;
}
.home .data-section .temp-data span:nth-child(1) {
  font-size: 5rem;
}
.home .data-section .temp-data span:nth-child(2) {
  margin-top: -35px;
}
.home .data-section .weather-data {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding-left: 10%;
  font-size: 18px;
  line-height: 1.6;
}
